@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  background: #f5f6fa;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav-bar {
  display: flex;
  align-items: center;
  background-color: #273c75;
  height: 6vh;
}

.logo {
  margin-left: 30px;
  font-size: 20px;
  font-weight: 600;
  color: #f5f6fa;
}

.footer-bar {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-color: #273c75;
  height: 4vh;
}

.footer-text {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-right: 30px;
  font-size: 16px;
  font-weight: 500;
  color: #f5f6fa;
}

a {
  margin: 0 8px 0 5px;
  text-decoration: none;
  color: #f5f6fa;
}

@media screen and (max-width: 600px) {
  .parent-wrapper {
    flex-direction: column;
  }
}

.divider {
  margin: 5px 0px;
  height: 2px;
  width: 100%;
  background-color: rgb(24, 24, 24);
}

.print-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.print-btn {
  position: relative;
  left: 45%;
  top: 60px;
  margin-top: 20px;
  width: 100px;
  border: none;
  outline: none;
  padding: 8px 0px;
  border-radius: 10px;
  background: #273c75;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.parent-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-wrapper {
  margin: 20px 30px;
  width: 80%;
  height: 100%;
  background: white;
}

.preview-wrapper {
  margin: 20px 30px;
  width: 80%;
  height: 100%;
  background: white;
}

.bio-wrapper {
  padding: 20px 20px;
}

input {
  outline: none;
  border: none;
  background: white;
}

.fname {
  font-size: 30px;
  font-weight: bold;
  margin-bottom: 5px;
}

.bio-dets {
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 500;
  background-color: #f5f6fa;
  padding: 10px 8px;
}

.bio {
  display: flex;
  flex-direction: column;
}

.bio-btn {
  margin-top: 10px;
  border: none;
  outline: none;
  padding: 8px 0px;
  border-radius: 10px;
  background: #273c75;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.toggle-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  top: 1vh;
  align-items: center;
}

.toggle {
  position: relative;
  border: none;
  width: 80px;
  outline: none;
  padding: 8px 0px;
  border-radius: 10px;
  background: #273c75;
  color: white;
  font-weight: 400;
  cursor: pointer;
}

.p-name {
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 10px;
}

.p-pdets {
  font-size: 16px;
  font-weight: 500;
}

.work-wrapper {
  margin: 40px 0;
}

.p-w1 {
  font-size: 26px;
  font-weight: 600;
}

.p-cname {
  font-size: 22px;
  font-weight: 600;
}

.p-cdets {
  font-size: 16px;
  font-weight: 500;
}

.datewrapper {
  display: flex;
}

.p-cdate1,
.p-cdate2 {
  margin-right: 4px;
  font-size: 15px;
  font-weight: 400;
}

.edu-wrapper {
  margin: 40px 0;
}

.p-e1 {
  font-size: 26px;
  font-weight: 600;
}

.p-ename {
  font-size: 22px;
  font-weight: 600;
}

.p-edeg {
  font-size: 16px;
  font-weight: 500;
}

.datewrapper {
  display: flex;
}

.p-edate1,
.p-edate2 {
  margin-right: 4px;
  font-size: 15px;
  font-weight: 400;
}

